import React from "react";
import RImage from "../elementary/img.jsx";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default ({classCSS = 'speakers-slide col-10 col-sm-6 col-lg-4', post, index, onClick }) => {
  if (!post) {
    return null;
  }
  const { frontmatter: data } = post;
  return (
    <div
      className={classCSS}
      itemProp="performer"
      itemScope=""
      itemType="http://schema.org/Person"
      aria-hidden="true"
      tabIndex="0"
      key={index}
      onClick={onClick}
    >
      <div className="row">
        <div className="speakers-slide__item">
          <div className="speakers-slide__img-wrapper">
            {data.image && (
              <RImage imageInfo={data && data.image ? data.image : null} />
            )}
          </div>
          <div className="speakers-slide__info-wrapper">
            <div className="speakers-slide__info">
              <h3 className="speakers-slide__info-title">
                <span className="speakers-slide__info-title-name">
                  {data.name ? data.name : null}
                </span>
              </h3>
              <p className="speakers-slider__info__place-of-work">
                {data.positions &&
                  data.positions.map(item => (
                    <>
                      <span className="speakers-slider__info-position">
                        {item.position ? item.position : null}
                        {item.company.trim() ? "@" : null}
                      </span>
                      <span className="speakers-slider__info-company">
                        <u>{item.company.trim() ? item.company : null}</u>
                      </span>
                      <br />
                    </>
                  ))}
              </p>
              <p className="speakers-slider__info__place">
                <span className="speakers-slider__info-country">
                  {data.country ? data.country : null}
                  {data.city ? "," : null}
                </span>
                <span className="speakers-slider__info-city">
                  {data.city ? data.city : null}
                </span>
              </p>
              <p className="speakers-slider__info-rept font-weight-bold">
                {data.reports &&
                  data.reports.map(item => (
                    <>
                      {item.title ? item.title : null}
                      <span className="info-rept__lang">
                        [{item.lang ? item.lang : null}]
                        <br />
                      </span>
                    </>
                  ))}
                <br /> <br />
              </p>
              <div className="speakers-slide__info-links">
                {data.socials &&
                  data.socials.map(item => (
                    <a
                      className="speaker__link"
                      href={item.link}
                      target="_blank"
                    >
                      <FontAwesomeIcon
                        icon={["fab", item.icon]}
                        className="fa"
                      />
                    </a>
                  ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
