import React from 'react'
import PropTypes from 'prop-types'
import Img from 'gatsby-image'

const RImage = (data) => {
    if (!data || !data.imageInfo) {
        return null;
    }
    const { style = '', imageInfo: { alt = '', childImageSharp, image } = {}} = {} = data;

    if (!!image && !!image.childImageSharp) {
        return (
            <Img style={style} fluid={image.childImageSharp.fluid} alt={alt} />
        );
    }

    if (!!childImageSharp) {
        return <Img style={style} fluid={childImageSharp.fluid} alt={alt} />;
    }

    if (!!image && typeof image === 'string')
        return <img style={style} src={image} alt={alt} />;
    return null;
}

RImage.propTypes = {
    style: PropTypes.object,
    imageInfo: PropTypes.shape({
        alt: PropTypes.string,
        childImageSharp: PropTypes.object,
        image: PropTypes.oneOfType([PropTypes.object, PropTypes.string]).isRequired,
    }).isRequired,
}

export default RImage
