import React from "react";
import Tiket from "../parts/tickets-item.jsx";

export default ({ data }) => (
  <section className="tickets_section">
    <div className="container">
      <div className="text_block">
        <p className="title mb-5">{data.title ? data.title : null}</p>
        <p className="description">{data.subtitle ? data.subtitle : null}</p>
        <div
            className="description small"
          dangerouslySetInnerHTML={{ __html: data.description }}
        ></div>
      </div>
      <div className ="tickets_block">
        {data.tickets.map((item, index) => (
          <Tiket key={index} index={index} data={item} />
        ))}
      </div>
    </div>
  </section>
);
