class ScheduleList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      post: {},
      currentIndex: null
    };
  }

  render() {
    const { data } = this.props;
    const { edges: posts } = data.allMarkdownRemark;
    let even = false;

    return (
      <div className="row">
        <div className="speakers-title offset-1 col-10">
          <h1 className="text-center text-uppercase main_title">
            Schedule
          </h1>
        </div>
          <div className="row schedule-row" role="listbox">
            {posts &&
              posts
                .filter(({ node }) => !!node)
                .map(({ node: post }) => (
                  <div className={'post'} key={post.frontmatter.day}>
                    <div className={'day-title'}>{post.frontmatter.title}</div>
                    <div className={'schedule-body'}>
                      {
                        post.frontmatter.events.map((itm, index) => {
                          even = !even;
                          return (
                              <div key={itm.timeFrom + index} className={'event ' + (itm.flow !== 'first' ? 'even' : 'odd') }>
                                <div className={'left'} >
                                  {!itm.break &&
                                  <span className={'time'}>
                                    {itm.timeFrom} - {itm.timeTo}
                                  </span>
                                  }
                                </div>
                                <div className={'right'} >
                                  <div>{itm.title}</div>
                                  <div className={'desc'}>{itm.description || '  '}</div>
                                </div>
                              </div>
                          );
                        })
                      }
                    </div>
                  </div>
                ))}
          </div>
      </div>
    );
  }
}

ScheduleList.propTypes = {
  post: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      edges: PropTypes.array
    })
  })
};

export default () => (
  <StaticQuery
    query={graphql`
      query ScheduleListQuery {
        allMarkdownRemark(
          sort: { fields: [frontmatter___day] }
          filter: { frontmatter: { templateKey: { eq: "schedule-day" } } }
        ) {
          edges {
            node {
              excerpt(pruneLength: 400)
              id
              fields {
                slug
              }
              frontmatter {
                title
                day(formatString: "MMMM DD")
                templateKey
                events {
                  timeFrom
                  timeTo
                  title
                  description
                  break
                  flow 
                }
              }
            }
          }
        }
      }
    `}
    render={(data, count) => <ScheduleList data={data} count={count} />}
  />
);
