import React from "react";
import RImage from "../elementary/img.jsx";
import { faLink } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default ({ post, index }) => {
  if (!post) {
    return null;
  }
  // console.log(post);
  const { frontmatter: data } = post;
  return (
    <div
      className="chairman-info insides-wrapper slick-slide"
      style={{ width: "278px" }}
    >
      <div className="img-wrapper">
        {data.image && (
          <RImage imageInfo={data && data.image ? data.image : null} />
        )}
      </div>
      <h2 className="chairman__name">{data.name}</h2>
      <p className="chairman__position">{data.position}</p>
      <div className="chairman__link-list">
        {data.socials &&
          data.socials
            .map(({ link, icon }, index) => (
                <a
                    className="chairman__link"
                    href={link}
                    target="_blank"
                >
                  <FontAwesomeIcon
                      icon={icon === 'custom' ? faLink : ["fab",  icon]}
                      className="fa"
                  />
                </a>
            ))}
      </div>
    </div>
  );
};
