import React from "react";
import RImage from "../elementary/img.jsx";

const getImage = (image) => {
    if (!image.childImageSharp && image.extension === 'svg' && image.publicURL) {
        return (<img src={image.publicURL}/>);
    }
    return (<RImage imageInfo={image ? image : null} />);
};
export default ({ post, index }) => {
  if (!post) {
    return null;
  }

  const { frontmatter: data } = post;
  return (
    <div className="col-6 col-md-4 text-center thank-box mx-auto">
      <a className="w-100" href={data.link} target="_blank">
          {
              data.image && getImage(data.image)
          }
      </a>
    </div>
  );
};
