import React from "react";
import { graphql, StaticQuery } from "gatsby";
import { SponsorsRoll } from "./parts/SponsorsRoll";

export default () => (
  <StaticQuery
    query={graphql`
      query MediaPartnerRollQuery {
        allMarkdownRemark(
          sort: { order: DESC, fields: [frontmatter___date] }
          filter: { frontmatter: { templateKey: { eq: "partner-post" }, type: { eq: "MEDIA PARTNERS" } } }
        ) {
          edges {
            node {
              excerpt(pruneLength: 400)
              id
              fields {
                slug
              }
              frontmatter {
                link
                image {
                  childImageSharp {
                    fluid(maxHeight: 225, quality: 100) {
                      ...GatsbyImageSharpFluid
                    }
                  }
                }
              }
            }
          }
        }
      }
    `}
    render={(data, count) => <SponsorsRoll data={data} count={count} />}
  />
);
