import React from "react";
import PropTypes from "prop-types";
import { graphql, StaticQuery } from "gatsby";
import VideoItem from "./parts/video-item";
import Slider from "react-slick";

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

class VideoRoll extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: "",
      post: {},
      currentIndex: null
    };
  }

  render() {
    const { data } = this.props;
    const { edges: posts } = data.allMarkdownRemark;

    const settings = {
          dots: false,
          infinite: true,
          speed: 500,
          slidesToShow: 3,
          slidesToScroll: 2,
          responsive: [
              {
                  breakpoint: 1366,
                  settings: {
                      slidesToShow: 3,
                      slidesToScroll: 2,
                      // infinite: false,
                      dots: false
                  }
              },
              {
                  breakpoint: 1000,
                  settings: {
                      slidesToShow: 2,
                      slidesToScroll: 1,
                      initialSlide: 1
                  }
              },
              {
                  breakpoint: 480,
                  settings: {
                      slidesToShow: 1,
                      slidesToScroll: 1
                  }
              }
          ]
      };

    return (
      <div className="row">
        <div className="speakers-title offset-1 col-10">
          <h1 className="text-center text-uppercase main_title">
              That's how Community speaks about OdessaJS
          </h1>
        </div>
        <div
          id="speakers-carousel"
          className="speakers-carousel speakers-slider slide w-100 col-12"
          data-ride="carousel"
        >
              <Slider {...settings}>
                {posts &&
                  posts
                    .filter(({ node }) => !!node)
                    .map(({ node: post }, index) => {
                        return (
                            <VideoItem
                                classCSS={''}
                                key={index}
                                post={post}
                                onClick={() => window.open(post.frontmatter.url, "_blank")}
                            />
                        );
                    })}
              </Slider>
        </div>
      </div>
    );
  }
}

VideoRoll.propTypes = {
  post: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      edges: PropTypes.array
    })
  })
};

export default () => (
  <StaticQuery
    query={graphql`
      query VideoRollQuery {
        allMarkdownRemark(
          sort: { order: DESC, fields: [frontmatter___date] }
          filter: { frontmatter: { templateKey: { eq: "video-post" } } }
        ) {
          edges {
            node {
              excerpt(pruneLength: 400)
              id
              fields {
                slug
              }
              frontmatter {
                title
                templateKey
                description
                url
                image {
                  childImageSharp {
                    fluid(maxHeight: 350, quality: 100) {
                      ...GatsbyImageSharpFluid
                    }
                  }
                }
              }
            }
          }
        }
      }
    `}
    render={(data, count) => <VideoRoll data={data} count={count} />}
  />
);
