import React from "react";

export default ({ data, index }) => (
  <a
    href={data.link ? data.link : "https://odessajs2020.ticketforevent.com/"}
    className={`ticket ${data.color === "red" ? "red_ticket" : null} ${
      data.color === "blue" ? "blue_ticket" : null
    }`}
    id={`ticket_${index}`}
  >
    <h6 className="ticket_name">{data.name ? data.name : null}</h6>
    <p className="ticket_subname">{data.subname ? data.subname : null}</p>
    <p className="ticket_desc">{data.desc ? data.desc : null}</p>
    <p className="ticket_price">{data.price ? data.price : null}</p>
    <p className="ticket_currency">{data.currency ? data.currency : data.price ? "UAH" : null}</p>
    <p className="ticket_promo">{data.promo ? data.promo : null}</p>
    <div className="ticket_action">{data.action ? data.action : "BUY"}</div>
  </a>
);
