import React from "react";
import RImage from "../elementary/img.jsx";

export default ({ post, index }) => {
  if (!post) {
    return null;
  }
  const { frontmatter: data } = post;
  return (
    <div className="col-12 col-md-6 col-lg-4 text-center thank-box mx-auto">
      <p className="thank-box__title">
        <strong>{data.title}</strong>
      </p>
      <p className="thank-box__discount">
        {data.discount}
      </p>
      <a
        href="{data.link}"
        target="_blank"
        className="img-wrapper"
      >
        {data.image && (
            <RImage style={{maxHeight: '150px'}} imageInfo={data && data.image ? data.image : null} />
        )}
      </a>
      <a className="mail-to" href="mailto:{data.email}">
        <strong>{data.email}</strong>
      </a>
    </div>
  );
};
