import React from "react";
import RImage from "../elementary/img";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default ({ show, post, onClose, prevSpeaker, nextSpeaker, nextTitle = 'Next Speaker', prevTitle = 'Previous Speaker' }) => {
  if (!post.frontmatter) {
    return null;
  }
  const { frontmatter: data } = post;
  const {
    name,
    about,
    positions,
    company,
    country,
    city,
    image,
    reports,
    socials,
  } = data;
  return (
    <div
      className={`speakers-info-popup modal fade ${show}`}
      id="speaker-modal"
      style={{ display: show === "show" ? "block" : "none" }}
      onClick={event => {
        if (event.target.id === "speaker-modal") {
          onClose();
        }
      }}
    >
      <div className="modal-dialog modal-lg">
        <div className="modal-content">
          <div className="modal-header">
            <button
              type="button"
              className="close"
              data-dismiss="modal"
              aria-label="Close"
              onClick={onClose}
            >
              <span aria-hidden="true">×</span>
            </button>
            <div className="img-wrapper w-25">
              {image && <RImage imageInfo={image ? image : null} />}
            </div>
            <div className="info-wrapper">
              <div className="speaker-info">
                <h2>
                  <span className="speaker__name">{name}</span>
                </h2>
                <p className="speaker__position">
                  {positions &&
                    positions.map(item => (
                      <>
                        <span className="speakers-slider__info-position">
                          {item.position ? item.position : null}
                          {item.company ? "@" : null}
                        </span>
                        <span className="speakers-slider__info-company">
                          <u>{item.company ? item.company : null}</u>
                        </span>
                        <br />
                      </>
                    ))}
                </p>
                <p className="speaker__company">{company}</p>
                <p>
                  <span className="speaker__country">{country}</span>,{" "}
                  <span className="speaker__city">{city}</span>
                </p>
              </div>
              <div className="speaker__link-list">
                {socials &&
                  socials.map(item => (
                    <a
                      className="speaker__link"
                      href={item.link}
                      target="_blank"
                    >
                      <FontAwesomeIcon
                        icon={["fab", item.icon]}
                        className="fa"
                      />
                    </a>
                  ))}
              </div>
            </div>
            <div className="bg-img">
              <img src="img-min/speakers-waves.png" alt="" />
            </div>
          </div>
          <div className="modal-body">
            <div className="modal-body__reports">
              {reports &&
                reports.map(val => (
                  <>
                    <h2 className="modal-body__title">
                      {val.title}
                      <span className="title__lang">[{val.lang}]</span>
                    </h2>
                    <div className="modal-body__text">{val.description}</div>
                  </>
                ))}
            </div>
            <div className="speaker-text">
              {about &&
                <>
                  <h2 className="modal-body__title">About Speaker</h2>
                  <p className="modal-body__text">{about}</p>
                </>
              }
            </div>
          </div>
          <div className="modal-footer">
            <button type="button" className="prev" onClick={prevSpeaker}>
              <img src="img-min/speakers-slider-left.png" alt="" /> {prevTitle}
            </button>
            <button type="button" className="next" onClick={nextSpeaker}>
              {nextTitle}{" "}
              <img src="img-min/speakers-slider-right.png" alt="" />
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};
