import React from "react";
import RImage from "../elementary/img.jsx";

export default ({classCSS = 'speakers-slide col-10 col-sm-6 col-lg-4', post, index, onClick }) => {
  if (!post) {
    return null;
  }
  const { frontmatter: data } = post;
  return (
    <div
      className={classCSS}
      itemProp="performer"
      itemScope=""
      itemType="http://schema.org/Person"
      aria-hidden="true"
      tabIndex="0"
      key={index}
      onClick={onClick}
    >
      <div className="row">
        <div className="speakers-slide__item no-shadow">
          <div className="speakers-slide__img-wrapper min-height175 no-gray">
            {data.image && (
              <RImage imageInfo={data && data.image ? data.image : null} />
            )}
          </div>
          <div className="speakers-slide__info-wrapper">
            <div className="speakers-slide__info">
              <h3 className="speakers-slide__info-title">
                <span className="speakers-slide__info-title-name">
                  {data.title ? data.title : null}
                </span>
              </h3>
              <p className="speakers-slider__info__place-of-work">
                {data.description ? data.description : null}
              </p>

            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
