import React from "react";
import PropTypes from "prop-types";
import { graphql, StaticQuery } from "gatsby";
import Speaker from "./parts/speakers-item";
import SpeakerModal from "./parts/speakers-modal";

const getSpeaker = (surrentIndex, step, all) => {
  const nextIndex = surrentIndex + step;
  if (nextIndex < 0) {
    return {post: all[all.length - 1].node, currentIndex: all.length - 1};
  }
  if (nextIndex >= all.length) {
    return {post: all[0].node, currentIndex: 0};
  }
  return {post: all[nextIndex].node, currentIndex: nextIndex};
};

class SpeakerRoll extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: "",
      post: {},
      currentIndex: null
    };
  }

  render() {
    const { data } = this.props;
    const { edges: posts } = data.allMarkdownRemark;

    return (
      <div className="row">
        <div className="speakers-title offset-1 col-10">
          <h1 className="text-center text-uppercase main_title">
            {/*{data.title ? data.title : null}*/}
            Workshop
          </h1>
        </div>
        <SpeakerModal
          show={this.state.show}
          post={this.state.post}
          onClose={() => this.setState({ show: "", post: {} })}
          nextSpeaker={() => this.setState(getSpeaker(this.state.currentIndex, 1, posts))}
          prevSpeaker={() => this.setState(getSpeaker(this.state.currentIndex, -1, posts))}
          nextTitle={'Next Workshop'}
          prevTitle={'Previous workshop'}
        />
        <div
          id="speakers-carousel"
          className="speakers-carousel slide w-100 col-12"
          data-ride="carousel"
        >
          <div className="row" role="listbox" id="speakersSlider">
            {posts &&
              posts
                .filter(({ node }) => !!node)
                .map(({ node: post }, index) => (
                  <Speaker
                    key={index}
                    post={post}
                    onClick={() => this.setState({ show: "show", post, currentIndex: index })}
                  />
                ))}
          </div>
        </div>
        <div
          className={`modal-backdrop fade ${this.state.show}`}
          style={{ display: this.state.show === "show" ? "block" : "none" }}
        ></div>
      </div>
    );
  }
}

SpeakerRoll.propTypes = {
  post: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      edges: PropTypes.array
    })
  })
};

export default () => (
  <StaticQuery
    query={graphql`
      query WorkshopRollQuery {
        allMarkdownRemark(
          sort: { order: DESC, fields: [frontmatter___date] }
          filter: { frontmatter: { templateKey: { eq: "workshop-post" } } }
        ) {
          edges {
            node {
              excerpt(pruneLength: 400)
              id
              fields {
                slug
              }
              frontmatter {
                name
                templateKey
                socials {
                  icon
                  link
                }
                country
                city
                reports {
                  description
                  lang
                  title
                }
                positions {
                  company
                  position
                }
                date(formatString: "MMMM DD, YYYY")
                image {
                  childImageSharp {
                    fluid(maxHeight: 350, quality: 100) {
                      ...GatsbyImageSharpFluid
                    }
                  }
                }
              }
            }
          }
        }
      }
    `}
    render={(data, count) => <SpeakerRoll data={data} count={count} />}
  />
);
